.CodeMirror {
    color: $colorText
}

.CodeMirror-lines {
    padding: 0.5em 0
}

.CodeMirror pre {
    padding: 0 0.25em
}

.CodeMirror-cursor {
    border-left: 1px solid $colorText;
    border-right: none;
    width: 0
}

.CodeMirror div.CodeMirror-secondarycursor {
    border-left: 1px solid $colorMuted
}

.cm-fat-cursor .CodeMirror-cursor {
    width: auto;
    border: 0 !important;
    background: $colorMuted
}

.cm-fat-cursor div.CodeMirror-cursors {
    z-index: 1
}

.cm-animate-fat-cursor {
    width: auto;
    border: 0;
    -webkit-animation: blink 1.06s steps(1) infinite;
    -moz-animation: blink 1.06s steps(1) infinite;
    animation: blink 1.06s steps(1) infinite;
    background-color: $colorMuted
}

@-moz-keyframes blink {
    50% {
        background-color: transparent
    }
}

@-webkit-keyframes blink {
    50% {
        background-color: transparent
    }
}

@keyframes blink {
    50% {
        background-color: transparent
    }
}

.cm-tab {
    display: inline-block;
    text-decoration: inherit
}

.cm-s-default .cm-header {
    color: #00f
}

.cm-s-default .cm-quote {
    color: $colorEmphasis
}

.cm-negative {
    color: $colorDanger
}

.cm-positive {
    color: $colorEmphasis
}

.cm-header, .cm-strong {
    font-weight: $fontWeightBold
}

.cm-em {
    font-style: italic
}

.cm-link {
    text-decoration: underline
}

.cm-strikethrough {
    text-decoration: line-through
}

.cm-s-default .cm-keyword {
    color: #708
}

.cm-s-default .cm-atom {
    color: #219
}

.cm-s-default .cm-number {
    color: $colorEmphasis
}

.cm-s-default .cm-def {
    color: #00f
}

.cm-s-default .cm-variable-2 {
    color: #05a
}

.cm-s-default .cm-variable-3 {
    color: $colorEmphasis
}

.cm-s-default .cm-comment {
    color: #a50
}

.cm-s-default .cm-string {
    color: $colorDanger
}

.cm-s-default .cm-string-2 {
    color: #f50
}

.cm-s-default .cm-meta, .cm-s-default .cm-qualifier {
    color: $colorDarkBG4
}

.cm-s-default .cm-builtin {
    color: #30a
}

.cm-s-default .cm-bracket {
    color: #997
}

.cm-s-default .cm-tag {
    color: $colorEmphasis
}

.cm-s-default .cm-attribute {
    color: #00c
}

.cm-s-default .cm-hr {
    color: #999
}

.cm-s-default .cm-link {
    color: #00c
}

.cm-invalidchar, .cm-s-default .cm-error {
    color: $colorDanger
}

div.CodeMirror span.CodeMirror-matchingbracket {
    color: $colorEmphasis
}

div.CodeMirror span.CodeMirror-nonmatchingbracket {
    color: $colorDanger
}

.CodeMirror-matchingtag {
    background: rgba(255, 150, 0, .3)
}

.CodeMirror-activeline-background {
    background: $colorLightBG4
}

.CodeMirror {
    position: relative;
    overflow: hidden;
}

.CodeMirror-scroll {
    overflow: scroll !important;
    margin-bottom: -2em;
    margin-right: -2em;
    padding-bottom: 2em;
    height: 100%;
    outline: 0;
    position: relative
}

.CodeMirror-sizer {
    position: relative;
    border-right: 2em solid transparent
}

.CodeMirror-gutter-filler, .CodeMirror-hscrollbar, .CodeMirror-scrollbar-filler, .CodeMirror-vscrollbar {
    position: absolute;
    z-index: 6;
    display: none
}

.CodeMirror-vscrollbar {
    right: 0;
    top: 0;
    overflow-x: hidden;
    overflow-y: scroll
}

.CodeMirror-hscrollbar {
    bottom: 0;
    left: 0;
    overflow-y: hidden;
    overflow-x: scroll
}

.CodeMirror-scrollbar-filler {
    right: 0;
    bottom: 0
}

.CodeMirror-gutter-filler {
    left: 0;
    bottom: 0
}

.CodeMirror-gutters {
    position: absolute;
    left: 0;
    top: 0;
    min-height: 100%;
    z-index: 3
}

.CodeMirror-gutter {
    white-space: normal;
    height: 100%;
    display: inline-block;
    vertical-align: top;
    margin-bottom: -2em
}

.CodeMirror-gutter-wrapper {
    position: absolute;
    z-index: 4;
    background: 0 0 !important;
    border: none !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none
}

.CodeMirror-gutter-background {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 4
}

.CodeMirror-gutter-elt {
    position: absolute;
    cursor: default;
    z-index: 4
}

.CodeMirror-lines {
    cursor: text;
    min-height: 1px
}

.CodeMirror pre {
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    border-width: 0;
    background: 0 0;
    font-family: inherit;
    font-size: inherit;
    margin: 0;
    white-space: pre;
    word-wrap: normal;
    line-height: inherit;
    color: inherit;
    z-index: 2;
    position: relative;
    overflow: visible;
    -webkit-tap-highlight-color: transparent;
    -webkit-font-variant-ligatures: none;
    font-variant-ligatures: none
}

.CodeMirror-wrap pre {
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: normal
}

.CodeMirror-linebackground {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0
}

.CodeMirror-linewidget {
    position: relative;
    z-index: 2;
    overflow: auto
}

.CodeMirror-code {
    outline: 0
}

.CodeMirror-gutter, .CodeMirror-gutters, .CodeMirror-linenumber, .CodeMirror-scroll, .CodeMirror-sizer {
    -moz-box-sizing: content-box;
    box-sizing: content-box
}

.CodeMirror-measure {
    position: absolute;
    width: 100%;
    height: 0;
    overflow: hidden;
    visibility: hidden
}

.CodeMirror-cursor {
    position: absolute
}

.CodeMirror-measure pre {
    position: static
}

div.CodeMirror-cursors {
    visibility: hidden;
    position: relative;
    z-index: 3
}

.CodeMirror-focused div.CodeMirror-cursors, div.CodeMirror-dragcursors {
    visibility: visible
}

.CodeMirror-selected {
    background: $colorLightBG3
}

.CodeMirror-focused .CodeMirror-selected, .CodeMirror-line::selection, .CodeMirror-line > span::selection, .CodeMirror-line > span > span::selection {
    background: $colorLightBG3
}

.CodeMirror-crosshair {
    cursor: crosshair
}

.CodeMirror-line::-moz-selection, .CodeMirror-line > span::-moz-selection, .CodeMirror-line > span > span::-moz-selection {
    background: $colorMutedMore
}

.cm-searching {
    background: #ffa;
    background: rgba(255, 255, 0, .4)
}

.cm-force-border {
    padding-right: .1px
}

@media print {
    .CodeMirror div.CodeMirror-cursors {
        visibility: hidden
    }
}

.cm-tab-wrap-hack:after {
    content: ''
}

span.CodeMirror-selectedtext {
    background-color: rgba($colorEmphasis, 0.2)
}

.CodeMirror {
    min-height: 20em;
    z-index: 1
}

.CodeMirror-scroll {
    min-height: 20em;
}

.CodeMirror-fullscreen {
    background: #fff;
    position: fixed !important;
    top: 3.125em;
    left: 0;
    right: 0;
    bottom: 0;
    height: auto;
    z-index: 9;
    padding: 0.5em 1em
}

.CodeMirror-sided {
    width: 50% !important
}

.editor-toolbar {
    position: absolute;
    top: 0;
    left: 0;
    user-select: none;
    border-bottom: 1px solid $colorLightBG4;
    background-color: rgba($colorLightBG1, 0.9);
    width: 100%;
    padding: 0.5em 0;
    z-index: 50;
}

.editor-toolbar:after, .editor-toolbar:before {
    display: block;
    content: ' ';
    height: 1px
}

.editor-toolbar.fullscreen {
    width: 100%;
    height: 3.125em;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    opacity: 1;
    z-index: 9;
    background-color: $colorLightBG3;
    padding: 0.625em 1em;
}

.editor-toolbar.fullscreen::before {
    width: 1.25em;
    height: 3.125em;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0
}

.editor-toolbar.fullscreen::after {
    width: 1.25em;
    height: 3.125em;
    position: fixed;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0
}

.editor-toolbar a {
    display: inline-block;
    text-align: center;
    text-decoration: none !important;
    color: $colorOutline;
    width: 1.875em;
    height: 2em;
    cursor: pointer;
    @include transition(color $durationShort);
}

.editor-toolbar a.active, .editor-toolbar a:hover {
    color: $colorEmphasis
}

.editor-toolbar a:before {
    line-height: 2em
}

.editor-toolbar i.separator {
    display: inline-block;
    width: 0;
    border-left: 1px solid $colorLightBG4;
    color: transparent;
    text-indent: -0.625em;
    margin: 0 0.375em
}

.editor-toolbar.disabled-for-preview a:not(.no-disable) {
    pointer-events: none;
    border-color: transparent;
    text-shadow: inherit
}

@media screen and (max-width: $thresholdCollapse) {
    .editor-toolbar a.no-mobile {
        display: none
    }
}

.editor-preview, .editor-preview-side {
    padding: 1em;
    background: $colorLightBG2;
    overflow: auto;
    display: none;
    box-sizing: border-box;
}

.editor-preview {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 7;
    padding: 0.5em;
}

.editor-preview-side {
    position: fixed;
    bottom: 0;
    width: 50%;
    top: 3.125em;
    right: 0;
    z-index: 9;
}

.editor-preview-active, .editor-preview-active-side {
    display: block
}

.editor-preview-side > p, .editor-preview > p {
    margin-top: 0
}

.editor-preview pre, .editor-preview-side pre {
    margin-bottom: 0.625em
}

.editor-preview table td, .editor-preview table th, .editor-preview-side table td, .editor-preview-side table th {
    border: 1px solid $colorMutedMore;
    padding: 5px
}

.CodeMirror .CodeMirror-code .cm-tag {
    color: $colorEmphasis
}

.CodeMirror .CodeMirror-code .cm-attribute {
    color: #795da3
}

.CodeMirror .CodeMirror-code .cm-string {
    color: #183691
}

.CodeMirror .CodeMirror-selected {
    background: rgba($colorEmphasis, 0.2)
}

.CodeMirror .CodeMirror-code .cm-header-1 {
    font-size: 200%;
    line-height: 200%
}

.CodeMirror .CodeMirror-code .cm-header-2 {
    font-size: 160%;
    line-height: 160%
}

.CodeMirror .CodeMirror-code .cm-header-3 {
    font-size: 125%;
    line-height: 125%
}

.CodeMirror .CodeMirror-code .cm-header-4 {
    font-size: 110%;
    line-height: 110%
}

.CodeMirror .CodeMirror-code .cm-comment {
    background: rgba(0, 0, 0, .05);
    border-radius: 2px
}

.CodeMirror .CodeMirror-code .cm-link {
    color: $colorMuted
}

.CodeMirror .CodeMirror-code .cm-url {
    color: $colorMuted
}

.CodeMirror .CodeMirror-code .cm-strikethrough {
    text-decoration: line-through
}

.CodeMirror .CodeMirror-placeholder {
    opacity: .5
}

.CodeMirror .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
    background: rgba(255, 0, 0, .15)
}

.markdown-preview, .editor-preview, .editor-preview-side {
    p {
        margin-bottom: 1em;
    }

    a {
        color: $colorEmphasis;
    }

    h3 {
        font-size: 1.5em;
        font-weight: $fontWeightBold;
        margin-bottom: 0.5em;
    }

    img {
        max-width: 100%;
    }

    blockquote {
        background-color: $colorLightBG3;
        border-left: 0.5em solid $colorLightBG4;
    }

    ul, ol {
        padding-left: 1.5em;
        margin-bottom: 1em;
    }

    ol {
        list-style: decimal;
    }

    ul {
        list-style: circle;
    }

    code {
        background-color: $colorLightBG4;
        padding: 0 0.25em
    }

    table {
        margin-bottom: 1em
    }
}

.no-touch {
    .editor-preview, .editor-preview-side {
        a:hover {
            text-decoration: underline;
        }
    }
}