.chart {
    background-color: $colorEmphasis;
    color: $colorLightText;
    padding-bottom: 1em;
    position: relative;
    padding-bottom: 27.5%;

    &__info {
        text-align: center;
        font-size: 0.5em;
        color: $colorEmphasisDarker;
        font-family: $fontHeading;
        font-weight: $fontWeightBold;
        margin-bottom: 2em; }

    &__container {
        @include position(absolute, 2em null null 0);
        width: 100%;
        padding: 0 3em;
        opacity: 0;
        z-index: -1;
        @include transition(opacity $durationMedium, z-index 0ms ease $durationMediumUp);

        @media screen and (max-width: $thresholdCollapse) {
            padding: 0 2em; }
        @media screen and (max-width: $thresholdMicro) {
            padding: 0 1em; }

        &--active {
            opacity: 1;
            z-index: 1;
            @include transition(opacity $durationMedium); } } }
