.activity-feed {
    padding: 0 2em;
    line-height: 1.125em;

    a {
        color: $colorEmphasis;
        @include transition(opacity $durationShort); }

    @media screen and (max-width: $thresholdCollapse) {
        padding: 0 1em; } }

.activity {
    border-bottom: 1px solid $colorLightBG4;
    padding: 0.5em 0;

    &__actor, &__subject {
        display: inline-block;
        vertical-align: top;
        position: relative; }

    &__thumbnail {
        @include position(absolute, 0.625em null null -0.625em);
        @include size(2em);
        border-radius: 50%;
        overflow: hidden;
        z-index: 10;
        background-color: $colorDarkBG3;
        border: 0.125em solid $colorLightBG1;

        .document-thumbnail {
            border: 0;
            padding: 0; }
        .document-thumbnail__image {
            position: static;
            @include size(100%); }
        .document-thumbnail__icon {
            position: static;
            background-color: $colorDarkBG3;
            font-size: 1em;
            padding: 0.375em;
            top: 0;
            left: 0;
            display: inline-block; } }

    &__subject {
        width: calc(100% - 4em);
        padding-right: 1em;
        padding-bottom: 0.25em; }

    &__time {
        color: $colorMuted;
        font-size: 0.75em; }

    &__actor {
        padding: 0.25em 1em 0.25em 0;

        @media screen and (max-width: $thresholdMicro) {
            padding: 0.25em 0.75em 0.25em 0; } }

    &--minor {
        font-size: 0.875em; }

    &--empty {
        padding: 4em 2em;
        text-align: center; } }

.no-touch {
    .activity-feed a:hover {
        opacity: 0.7; } }
